.youtube {
  &:hover .youtube__play-button {
    background-color: #D42530;
  }
}
.youtube img {
  position: absolute;
  width: 100%;
  top: -16.82%;
  left: 0;
}
.youtube__play-button {
  width: 65px;
  height: 45px;
  background-color: rgba(41, 41, 41, .9);
  border-radius: 10px;
  border: none;
  transition: $transition-base;
  font-size: 0;
  z-index: 3;
}
.youtube__play-button:before {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 11px 0 11px 20px;
  border-color: transparent transparent transparent #fff;
}
.youtube img,
.youtube .play-button {
  cursor: pointer;
}
.youtube__play-button,
.youtube__play-button:before {
  position: absolute;
}
.youtube__play-button,
.youtube__play-button:before {
  top: 50%;
  left: 50%;
  transform: translate3d( -50%, -50%, 0 );
}
.video-data {
  padding-bottom: 21px;
  overflow: hidden;
  @include media-breakpoint-up(md) {
    padding-bottom: 38px;
  }
  @include media-breakpoint-up(xl) {
    padding-top: 82px;
    padding-bottom: 76px;
  }
  &--has-subtitle {
    .video-data__title {
      margin-bottom: 10px;
      @include media-breakpoint-up(md) {
        margin-bottom: 18px;
      }
      @include media-breakpoint-up(xl) {
        margin-bottom: 28px;
      }
    }
  }
  &--border-top {
    border-top: 1px solid #ebebeb;
    .video-data__title {
      margin-bottom: 18px;
      @include media-breakpoint-up(xl) {
        margin-bottom: 28px;
      }
    }
  }
  &__title {
    margin-bottom: 24px;
    @include media-breakpoint-up(md) {
      margin-bottom: 30px;
    }
    @include media-breakpoint-up(xl) {
      margin-bottom: 40px;
    }
  }
  &__slider {
    position: relative;
    .container {
      @include media-breakpoint-up(md) {
        padding-right: 0;
        margin-left: calc((100vw - 768px)/2);
        max-width: calc(768px + (100vw - 768px / 2));
      }
      @include media-breakpoint-up(lg) {
        margin-left: calc((100vw - 960px)/2);
        max-width: calc(960px + (100vw - 960px / 2));
      }
      @include media-breakpoint-up(xl) {
        margin-left: 0;
        max-width: 100%;
        padding-left: 96px;
      }
    }
  }
  &__list {
    margin-bottom: 0;
    padding: 0;
  }
  .slick-list {
    margin-right: -20px;
    margin-left: -20px;
    @include media-breakpoint-up(md) {
      margin: 0;
    }
  }
  .slick-slide {
    margin: 0 20px;
    font-size: 0;
    @include media-breakpoint-up(md) {
      width: calc(100vw - 40px - 110px - 34px - ((100vw - 768px)/2));
    }
    @include media-breakpoint-up(lg) {
      width: calc(100vw - 40px - 110px - ((100vw - 960px)/2));
    }
    @include media-breakpoint-up(xl) {
      margin-right: 25px;
      margin-left: 25px;
      width: 671px;
    }
  }
  &__footer {
    text-align: center;
    @include media-breakpoint-up(md) {
      padding-top: 20px;
    }
  }
  &__arrow {
    display: none;
    position: absolute;
    top: 50%;
    margin-top: -20px;
    @include media-breakpoint-up(xl) {
      display: flex;
    }
  }
  .slick-dots {
    @include media-breakpoint-up(md) {
      margin-left: -5px;
      margin-bottom: 0;
    }
  }
  &__label {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    padding: 12px 66px 12px 16px;
    color: #fff;
    z-index: 2;
    font-size: 14px;
    line-height: 1.43;
    font-family: "Open Sans", Intro, sans-serif;
    @include media-breakpoint-up(md) {
      padding: 24px 32px;
      font-size: 15px;
      line-height: 1.33;
    }
    p{
      margin-bottom: 0;
    }
    span, p {
      font-size: inherit;
      line-height: inherit;
      font-family: inherit;
    }
    &::before {
      content: '';
      position: absolute;
      right: 0;
      left: 0;
      bottom: 0;
      top: 0;
      background-color: rgba(#000, .5);
      z-index: -1;
    }
    + .youtube__play-button {
      @include media-breakpoint-down(sm){
        width: 46px;
        height: 32px;
        border-radius: 5px;
        transform: translate(-50%, 0)
      }
      &::before {
        @include media-breakpoint-down(sm) {
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 7.5px 0 7.5px 15px;
          border-color: transparent transparent transparent #ffffff;
        }
      }
    }
    p, span {
      color: inherit;
    }

  }
}
